<template>
  <div>
   
    <div class="sideMenu">
      <nav>
        <a href="#">
          <li class="nav-item">
            <router-link to="/admin/products" class="nav-link">
              <i class="fas fa-box-open"></i> 
            </router-link>
          </li>
        </a>
        <a href="#">
          <li class="nav-item">
            <router-link to="/admin/order" class="nav-link">
              <i class="fas fa-list-alt"></i> 
            </router-link>
          </li>
        </a>
        <a href="#">
          <li class="nav-item">
            <router-link to="/admin/coupons" class="nav-link">
              <i class="fas fa-ticket-alt"></i> 
            </router-link>
          </li>
        </a>
      </nav>
      <label for="sideMenu-active">
        <i class="fas fa-angle-right"></i>
      </label>
      <!-- label  nav  -->
    </div>
  </div>
</template>

<script>
export default {
  name: "navbar",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  methods: {
    signout() {
      const api = `${process.env.APIPATH}/logout`;
      const vm = this;
      this.$http.post(api).then(response => {
        console.log(response.data);
        if (response.data.success) {
          vm.$router.push("/Login");
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
html,
body {
  height: 100%;
}

body {
  background-color: #546377;
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif;
}

.container {
  width: 100%;
  padding-right: 15px;
  margin-left: -15px;
}

a {
  text-decoration: none;
  font-weight: 300;
  &:hover {
    color: yellow;
  }
}
a,
label {
  cursor: pointer;
}

.sideMenu {
  width: 300px;
  background-color: #ff7575;
  border-right: 3px solid #d1d1d1;
  display: flex;
  top: 95px;
  flex-direction: column; //
  // padding: 50px 0;
  box-shadow: 5px 0 5px rgba(23, 23, 54, 0.6);
  position: fixed; // label 
  transform: translateX(-100%); //  sideMenu  100%
  transition: 0.5s; // 

  form {
    display: flex;
    margin: 0 10px 50px;
    border-radius: 100px;
    border: 1px solid #fff;
    input {
      width: 230px;
    }
    button {
      width: 50px;
    }
    input,
    button {
      border: none;
      padding: 5px 10px;
      background-color: transparent;
      color: #fff;
      &:focus {
        outline: none;
      }
    }
  }

  label {
    width: 20px;
    height: 80px;
    background-color: #d1d1d1;
    color: #686666;
    //  sideMenu 
    position: absolute;
    right: -20px;
    top: 0;
    bottom: 0;
    margin: auto;
    line-height: 80px;
    text-align: center;
    border-radius: 0 5px 5px 0;
    box-shadow: 5px 0 5px rgba(23, 23, 54, 0.6);
  }
}

#sideMenu-active:checked + .sideMenu {
  transform: translateX(0);

  //  sideMenu checkbox  id
  label .fas {
    transform: scaleX(-1); //  label 
  }
}

#sideMenu-active {
  position: absolute;
  opacity: 0; // checkbox 
  z-index: -1; // 
}

nav {
  a {
    display: block;
    color: #fff;
    padding: 20px 10px;
    position: relative;

    .fas {
      margin-right: -1.1em;
      transform: scale(0);
      //
      transition: 0.3s;
    }

    &:hover .fas {
      margin-right: 0em; //
      transform: scale(1); //
    }

    & + a::before {
      content: "";
      position: absolute;
      border-top: 1px dashed #ffffff;
      left: 10px;
      right: 10px;
      top: 0px;
    }
  }
}
</style>



